body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-weight: 500;
  display: flex;
}

.header__img {
  display: block;
}

.nav__list {
  text-transform: uppercase;
  align-items: center;
  margin: 0;
  display: flex;
}

.nav__item {
  margin-left: 20px;
  list-style: none;
}

.nav__item:first-child {
  margin: 0;
}

.nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  transition: all .3s linear;
  display: flex;
  position: relative;
}

.nav__link:visited {
  color: #060b35;
}

.nav__link.is-active, .nav__link:hover {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.catalog {
  --coloumn-count: 1;
  box-sizing: border-box;
  grid-template-columns: repeat(var(--coloumn-count), min-content);
  grid-template-rows: repeat(2, min-content);
  place-content: center;
  gap: 48px 46px;
  padding-block: 50px;
  padding-inline: 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --coloumn-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --coloumn-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --coloumn-count: 4;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  gap: 40px;
  width: 200px;
  padding-block: 32px 16px;
  transition: all .3s linear;
  display: flex;
}

.card__img {
  box-sizing: border-box;
  padding-inline: 19px;
  display: flex;
}

.card__info {
  flex-direction: column;
  justify-content: space-between;
  gap: 24px;
  padding-inline: 16px;
  display: flex;
}

.card__overview {
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  display: flex;
}

.card__titles {
  flex-direction: column;
  justify-content: space-between;
  gap: 4px;
  display: flex;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__stars {
  display: flex;
}

.card__num-of-reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__buy {
  flex-direction: column;
  justify-content: space-between;
  gap: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__text-price {
  color: #616070;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__amount {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  box-sizing: border-box;
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s linear;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  border-radius: 5px;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

